import React, { Component } from 'react';
import { BrowserRouter as Router, Link } from "react-router-dom";
import authService from '../services/auth.service';
import axios from 'axios'

class Header extends Component {


  constructor(props) {
    super(props);
    this.routeChange = this.routeChange.bind(this);
    this.logOut = this.logOut.bind(this);


    this.state = {
      message: []
    };
  }
  state = {
    navigate: false
  };

  componentDidMount() {


    const mytoken = JSON.parse(localStorage.getItem('user'));
    const token = mytoken.token;
    //call the States and LGAS at the first 
    axios.get('https://gfso.chinikiguard.com/core/api/user/notification/list/',
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
        },

      },
    )
      .then(res => {
        if (res.data) {
          localStorage.setItem("notification", JSON.stringify(res.data));
        }
        this.setState({
          notification: res.data.length,
          message: res.data
        });
        // console.log(res.data)                  

      })

  }


  logOut() {
    authService.logout();

    this.setState({ navigate: true });
    window.location.reload("/login");
  }

  routeChange = () => {
    this.setState({ navigate: true });
    window.location = "/dashboard"
  };

  ViewNotification(message){

    const notifyId = message.pk
    console.log(message.pk)
    localStorage.setItem("notify_Update_ID", JSON.stringify(message.pk));
    const mytoken = authService.getCurrentUser();
    const token = mytoken.token;

    this.setState({
        loadingModal: true,
      });
    axios.get(`https://gfso.chinikiguard.com/core/api/user/notification/details/${notifyId}/`,
        {
            headers:
            {
                'Authorization': `Token ${token}`,
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                'Access-Control-Allow-Credentials': true
            },
        })
        .then(res => {
            window.location = "/Notifications"

            if (res.data) {
                localStorage.setItem("ApproveNotifications", JSON.stringify(res.data));
            }

            this.setState({
              ApproveThisSurvey: res.data,
              loadingModal: false,
            });

          })

  }

  render() {

    // const { navigate } = this.state;

    return (

      <Router>
        {/* {currentUser && ( */}

        <nav className="main-header navbar navbar-expand navbar-white navbar-light">
          {/* Left navbar links */}
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" data-widget="pushmenu" to="#" role="button"><i className="fas fa-bars" /></Link>
            </li>
            {/* <li className="nav-item d-none d-sm-inline-block">
      <a href="../../index3.html" className="nav-link">Home</a>
    </li> */}
            {/* <li className="nav-item d-none d-sm-inline-block">
      <a href="#" className="nav-link">Menu</a>
    </li> */}
          </ul>
          {/* Right navbar links */}
          <ul className="navbar-nav ml-auto">
            {/* Navbar Search */}

            {/* Messages Dropdown Menu */}
            <li className="nav-item dropdown">
              <a className="nav-link" data-toggle="dropdown" href="#">
                <i className="fas fa-bell" />
                <span className="badge badge-danger navbar-badge">{this.state.notification}</span>
              </a>
              <div className="dropdown-menu dropdown-menu-lg dropdown-menu-right">
                <span className="dropdown-item dropdown-header">{this.state.notification}&nbsp;Notifications</span>
                {this.state.message && this.state.message.map(message => (
                  <a>
                    <div className="dropdown-divider" />

                    <a
                     href="#" 
                     className="dropdown-item"
                     onClick={this.ViewNotification.bind(this, message)}

                     >

                      <i className="fas fa-envelope mr-2" /> {message.survey_title}{message.api_message}
                      <span className="float-right text-muted text-sm">{message.created}</span>
                    </a>
                  </a>

                ))}

                <div className="dropdown-divider" />
                <a href="#" className="dropdown-item dropdown-footer">See All Notifications</a>
              </div>
            </li>


            <li className="nav-item">
              <Link className="nav-link" data-widget="fullscreen" to="#" role="button">
                <i className="fas fa-expand-arrows-alt" />
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" data-slide="true" data-widget="control-sidebar" onClick={this.logOut}  >
                <span>Sign Out</span> &nbsp;
        <i className="fas fa-user-alt" />
              </Link>
            </li>
          </ul>
        </nav>

      </Router>

    )
  }

}


export default Header;



