import axios from 'axios'
import React, { Component} from "react";
import { Redirect } from 'react-router-dom';
import Header from './Header';
import Footer from './Footer'
import Menu from "./Menu";
import ReactPaginate from 'react-paginate';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';



 export default class ResponseTable extends Component {

    constructor(props) {
        super(props);
        this.handlePageClick = this.handlePageClick.bind(this);

        this.state = {
            offset: 0,
            tableData: [],
            orgtableData: [],
            perPage: 10,
            currentPage: 0,
            myloading: true
        };
    }

    //pagination
    handlePageClick = (e) => {
        const selectedPage = e.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({
            currentPage: selectedPage,
            offset: offset
        }, () => {
            this.loadMoreData()
        });

    };
    //pagination
    loadMoreData() {
        const data = this.state.orgtableData;

        const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
        this.setState({
            pageCount: Math.ceil(data.length / this.state.perPage),
            tableData: slice
        })

    }

    componentDidMount() {
        const scriptdashboard = document.createElement("script");
        scriptdashboard.src = "./plugins/datatables-buttons/js/dataTables.buttons.min.js";
        scriptdashboard.async = true;
        document.body.appendChild(scriptdashboard);

        const scriptChart = document.createElement("script");
        scriptChart.src = "./plugins/datatables-buttons/js/buttons.bootstrap4.min.js";
        scriptChart.async = true;
        document.body.appendChild(scriptChart);

        const scriptwidgets = document.createElement("script");
        scriptwidgets.src = "./plugins/jszip/jszip.min.js";
        scriptwidgets.async = true;
        document.body.appendChild(scriptwidgets);

        const scriptjquery = document.createElement("script");
        scriptjquery.src = "./plugins/pdfmake/pdfmake.min.js";
        scriptjquery.async = true;
        document.body.appendChild(scriptjquery);

        const pdfmakefont = document.createElement("script");
        pdfmakefont.src = "./plugins/pdfmake/vfs_fonts.js";
        pdfmakefont.async = true;
        document.body.appendChild(pdfmakefont);

        const datatables = document.createElement("script");
        datatables.src = "./plugins/datatables-buttons/js/buttons.html5.min.js";
        datatables.async = true;
        document.body.appendChild(datatables);

        const datatablesprint = document.createElement("script");
        datatablesprint.src = "./plugins/datatables-buttons/js/buttons.print.min.js";
        datatablesprint.async = true;
        document.body.appendChild(datatablesprint);

        const datatablesprintmin = document.createElement("script");
        datatablesprintmin.src = "./plugins/datatables-buttons/js/buttons.print.min.js";
        datatablesprintmin.async = true;
        document.body.appendChild(datatablesprintmin);

        const datatablescolVis = document.createElement("script");
        datatablescolVis.src = "./plugins/datatables-buttons/js/buttons.colVis.min.js";
        datatablescolVis.async = true;
        document.body.appendChild(datatablescolVis);

        this.Get_State_Data();

    }

    Get_State_Data() {

        const ax = axios.create({
            baseURL: 'http://localhost:3000/data'
        })
        ax.get('response.json')
            .then(res => {
                if (res.data) {
                    localStorage.setItem("ResponsesTable", JSON.stringify(res.data));
                }
                const tdata = res.data;
                // console.log(res.data.data)   

                const slice = tdata.slice(this.state.offset, this.state.offset + this.state.perPage)
                this.setState({
                    pageCount: Math.ceil(tdata.length / this.state.perPage),
                    orgtableData: tdata,
                    ResponseTable: slice,
                    myloading: false
                })
            });
        // console.log(res.data)   
    }

    render() {

        if (!localStorage.getItem('user')) {
            return (<Redirect to={'/'} />)
        }

        return (
            <div>
                <Header />
                <Menu />
                <div className="content-wrapper">
                    <br />
                    <section className="content">
                        <div className="container-fluid">

                            <div className="row">

                                <div className="col-12">

                                    <div className="card">
                                        <div className="card-header">
                                            <h3 className="card-title">Response Table</h3>
                                        </div>
                                        {/* /.card-header */}
                                        <div className="card-body">

                                            <table id="emp" class="table">
                                                <thead>
                                                    <tr>
                                                        <th>Section</th>
                                                        <th>Item</th>                                                            <br />

                                                        <th>Q1 2019</th>
                                                   

                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.ResponseTable && this.state.ResponseTable.map(category => (

                                                        <tr>
                                                            <td>
                                                                <b>{category.economic_code}</b>

                                                                {category.fields.map(economic => (
                                                                    <div>
                                                                <hr/>

                                                                    {economic.economic_code}
                                                                    </div>
                                                                ))}
                                                            </td>

                                                            <td>
                                                                <b>{category.name}
                                                                </b>
                                                                <br/>
                                                                {category.fields.map(field => (
                                                          <div style={{ fontWeight: 'lighter'}}>

                                                                 <hr/>
                                                                    { field.name }
                                                                    </div>

                                                                ))}

                                                            </td>

                                                            <br />
                                                            <td>
                                                            <br />

                                                                {category.fields.map(number => (
                                                                    <div>
                                                             <hr/>

                                                                        {number.value}
                                                                        </div>
                                                                ))}
                                                            </td>
                                                            <td>
                                                                
                                                            </td>

                                                        </tr>

                                                    ))}

                                                </tbody>

                                            </table>
                                            <div>
                                                <ReactHTMLTableToExcel
                                                    className="btn btn-info"
                                                    table="emp"
                                                    filename="ReportSurvey"
                                                    sheet="Sheet"
                                                    buttonText="Export excel" />
                                            </div>
                                            <ReactPaginate
                                                previousLabel={"prev"}
                                                nextLabel={"next"}
                                                breakLabel={"..."}
                                                breakClassName={"break-me"}
                                                pageCount={this.state.pageCount}
                                                marginPagesDisplayed={2}
                                                pageRangeDisplayed={5}
                                                onPageChange={this.handlePageClick}
                                                containerClassName={"pagination"}
                                                subContainerClassName={"pages pagination"}
                                                activeClassName={"active"} />

                                        </div>
                                    </div>
                                </div>


                            </div>


                        </div>


                    </section>
                </div>

                <Footer />
            </div>

        );
    }
}


