import React, {  useEffect } from "react";
// import { Line, Bar, Pie, Doughnut, HorizontalBar, Radar } from "react-chartjs-2";
// import axios from "axios";
// import Spinner from 'react-bootstrap/Spinner'
import { Redirect } from 'react-router-dom';
import Header from "./Header";
import Menu from "./Menu";
import Footer from "./Footer";




const Dashboard = () => {

  
 
 
  //using the functions
  useEffect(() => {

  
   
  }, []);

  if (!localStorage.getItem('user')) {
    return (<Redirect to={'/login'} />)
  }

  return (
    <div>
      <Header />

      <div className="content-wrapper">
        <Menu />
        <br />
        {/* Info boxes */}

        <div className="col-12">
        <div className="container-fluid">

          <div className="row">

            <div className="col-12 col-sm-6 col-md-3">
              <div className="info-box">
                <span className="info-box-icon   elevation-1"><i className="fas fa-desktop text-green" /></span>
                <div className="info-box-content">
                  <span className="info-box-text">Total responses</span>
                  <span className="info-box-number">
                    <div>
                     

                    </div>

                  </span>
                </div>
                {/* /.info-box-content */}
              </div>
              {/* /.info-box */}
            </div>
            {/* /.col */}
            <div className="col-12 col-sm-6 col-md-3">
              <div className="info-box mb-3">
                <span class="info-box-icon bg-pink"><i class="ion ion-stats-bars"></i></span>
                <div className="info-box-content">
                  <span className="info-box-text">Daily Responses</span>
                  <span className="info-box-number">
                    </span>
                </div>
                {/* /.info-box-content */}
              </div>
              {/* /.info-box */}
            </div>
            {/* /.col */}
            {/* fix for small devices only */}
            <div className="clearfix hidden-md-up" />
            <div className="col-12 col-sm-6 col-md-3">
              <div className="info-box mb-3">
                <span class="info-box-icon bg-warning"><i class="ion ion-stats-bars"></i></span>
                <div className="info-box-content">
                  <span className="info-box-text">Weekly Responses</span>
                  <span className="info-box-number">
                   
                  </span>
                </div>
                {/* /.info-box-content */}
              </div>
              {/* /.info-box */}
            </div>
            {/* /.col */}
            <div className="col-12 col-sm-6 col-md-3">
              <div className="info-box mb-3">
                <span class="info-box-icon bg-success"><i class="ion ion-stats-bars"></i></span>
                <div className="info-box-content">
                  <span className="info-box-text">Monthly Responses</span>
                  <span className="info-box-number">
                    

                  </span>
                </div>
                {/* /.info-box-content */}
              </div>
              {/* /.info-box */}
            </div>
            {/* /.col */}
          </div>
        </div>
     
        </div>

      </div>

      <Footer />
    </div>
  );
};

export default Dashboard;