import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'
import authService from "../services/auth.service";


const required = (value) => {
    if (!value) {
         return (
              <div className="alert alert-danger" role="alert">
                   This field is required!
              </div>
         );
    }
};

export default class Notifications extends Component {

    constructor(props) {
        super(props);
        
       
        this.state = {
            loading: false,
            message: "",
            myloading: false,
            AdoptSurveymessage: "",
            AdoptSurveysuccessful: false,
            AdoptSurveyloading: false,
            survey:""

        };
    }
    
  
   
    componentDidMount() {

        const mytoken = authService.getCurrentUser();
        const token = mytoken.token;
        axios.get("https://gfso.chinikiguard.com/survey/api/list/survey/?approved=yes?for_user=yes",
            {
                headers:
                {
                    'Authorization': `Token ${token}`,
                    'Access-Control-Allow-Origin': '*',
                    'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
                    'Access-Control-Allow-Credentials': true
                },
            })
            .then(res => {
                if (res.data) {
                    localStorage.setItem("ApprovedSurveyToUser", JSON.stringify(res.data));
                }
                // (res);
                // (res.data);
                this.setState({
                    ApprovedSurveyToUser: res.data,
                    myloading: false,
                    warningLoad: true,
                });
            })
    }

    
  

    render() {

        //check for user to access the page
        if (!localStorage.getItem('user')) {
            return (<Redirect to={'/login'} />)
          }
        //get the template details
        const ApproveNotifications = JSON.parse(localStorage.getItem('ApproveNotifications'));
        

        return (
            <div>
                <Header />
                <div className="content-wrapper" style={{ minHeight: 1602 }}>
                    <Menu /><br />
                    <section className="content">
  <div className="container-fluid">
    {/* Timelime example  */}
    <div className="row">
      <div className="col-md-12">
        {/* The time line */}
        

        <div className="timeline">
          {/* timeline time label */}
          <div className="time-label">
            <span className="bg-green">{ApproveNotifications.created}</span>
          </div>
          {/* /.timeline-label */}
          {/* timeline item */}
          <div>

            <i className="fas fa-envelope bg-blue" />
            
            <div className="timeline-item">
                 
              <span className="time text-green"><i className="fas fa-check" />{ApproveNotifications?ApproveNotifications.api_message:"no message available"} </span>
              <h3 className="timeline-header"> {ApproveNotifications?ApproveNotifications.subject:"no survey title"}</h3>
              <div className="timeline-body">
                {ApproveNotifications.message}
              </div>

            </div>
          </div>
          {/* END timeline item */}
         
          <div>
            <i className="fas fa-clock bg-gray" />
          </div>
        </div>
      </div>
      {/* /.col */}
    </div>
  </div>
  {/* /.timeline */}
</section>

                </div>
                <Footer />
            </div>
        );
    }
}



